import React from "react";
import "bootstrap/dist/css/bootstrap.css";
import { useState, useEffect } from "react";
import ListTeams from "../Components/ListTeams";
import Button from "../Components/Button";
import { getUsersFromDB, getUserWinsDB } from "../Functions/Database";
import { Reception4 } from "react-bootstrap-icons";
import "../App.css";

export default function Leaderboard() {
  const [players, setPlayers] = useState([]);

  useEffect(() => {
    setPlayers(JSON.parse(localStorage.getItem("Users")));
    // getUserWinsDB().then((users) => {
    //   setPlayers(users);
    //   localStorage.setItem("Users", JSON.stringify(users));
    // });
  }, []);
  {
    console.log(players);
  }
  return (
    <div>
      <ul className={"list-group"}>
        {players.map((Player) => (
          <li className="list-group-item" key={Player.Name}>
            {Player.Name + "Wins:" + Player.Win}
          </li>
        ))}
      </ul>
    </div>
  );
}
